import {observer} from "mobx-react-lite";
import {Button} from "@mui/material";
import React, {useEffect, useState} from "react";
import {useStore} from "../../store/useStore";
import style from "./Storage.module.scss";
import {useFormik} from "formik";
import TextField from "@mui/material/TextField";
import {FormikHelpers} from "formik/dist/types";
import {getDate} from "../../helpers/helpers";
import {IEvent, IValues} from "../../types/types";
import Typography from "@mui/material/Typography";
import {chainId, getContract, getContractAddress, getProvider} from "../../helpers/ethers.helper";
import Link from "@mui/material/Link";
import {svgIcons} from "../../assets/svgIcons";

export const Storage = observer(() => {
    const {
        cryptoStore: {
            value, setValue, currentAccountAddress
        },
        appStore: {
            errorHandler
        }
    } = useStore();

    const [events, setEvents] = useState<IEvent[]>([]);

    //========= GET VALUE =========//
    const getValue = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const network = await provider.getNetwork();

                //       -     
                if (network.chainId === chainId) {
                    const contract = getContract(provider);
                    const value = await contract.getValue();
                    setValue(value.toString());
                }
            }
        } catch (e: any) {
            errorHandler(e);
        }
    }

    //========= GET EVENTS =========//
    const getEventsHandler = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getContract(provider);
                const filter = contract.filters.ValueChanged();
                const logs = await contract.queryFilter(filter);
                //console.log(logs);
                setEvents(logs.map(({args}) => (args ? {
                    oldValue: args[0].toString(),
                    newValue: args[1].toString(),
                    timestamp: getDate(args[2].toNumber()),
                    address: args[3],
                } : {
                    oldValue: "-",
                    newValue: "-",
                    timestamp: "-",
                    address: "-",
                })))
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //========= ADD EVENT LISTENERS =========
    const addListener = async () => {
        try {
            if (window.ethereum) {
                const provider = getProvider();
                const contract = getContract(provider);
                const startBlockNumber = await provider.getBlockNumber();
                contract.on("ValueChanged", async (...args) => {
                    try {
                        console.log("ValueChanged")
                        const event = args[args.length - 1];
                        if (event.blockNumber > startBlockNumber) {
                            await getEventsHandler();
                        }
                    } catch (e: any) {
                        errorHandler(e)
                    }
                })
            }
        } catch (e: any) {
            errorHandler(e)
        }
    }

    //=========  =========//
    useEffect(() => {
        addListener().then(); //   
        getValue().then(() => {
        }); //   value
        getEventsHandler().then(() => {
        }); //   
    }, [window.ethereum]);

    //========= SET VALUE =========//
    const [valueChanging, setValueChanging] = useState(false);
    const initialValues: IValues = {
        value: 0
    }
    const onSubmit = async (values: IValues, formikHelpers: FormikHelpers<IValues>) => {
        try {
            if (currentAccountAddress && window.ethereum) {
                setValueChanging(true);
                const provider = getProvider();
                const signer = provider.getSigner(currentAccountAddress);
                const contract = getContract(provider);
                const tx = await contract
                    .connect(signer)
                    .setValue(values.value); //     value
                await tx.wait(); //   
                const value = await contract.getValue(); //   value
                setValue(value.toString()); //    value
            }
        } catch (e: any) {
            errorHandler(e);
        } finally {
            formikHelpers.resetForm();
            setValueChanging(false);
        }
    }
    const formik = useFormik({
        initialValues,
        onSubmit: onSubmit
    })

    return (
        <div className={style.storage}>
            <div className={style.contract}>
                <h2 className={style.blockTitle}>
                    Storage contract
                </h2>

                <div className={style.addressBlock}>
                    <Typography className={style.label}>Contract address</Typography>
                    <Typography className={style.address}>{getContractAddress()}</Typography>
                </div>

                <div className={style.etherscanBlock}>
                    {svgIcons.etherscan}
                    <Link className={style.link}
                          href="https://goerli.etherscan.io/address/0xBda254C68E684afB33e2f11CdC944Eb79DA29708"
                          target="_blank"
                    >
                        Link to Etherscan
                    </Link>

                </div>

                <div className={style.valueWrapper}>
                    <p className={style.label}>Value:</p>
                    <p className={style.value}>
                        <span>{value}</span>{valueChanging && <span>...value changing...</span>}
                    </p>
                </div>

                <form onSubmit={formik.handleSubmit}
                      className={style.form}
                >
                    <TextField fullWidth
                               size="small"
                               label="Value"
                               type="number"
                               inputProps={{
                                   min: 0
                               }}
                               {...formik.getFieldProps('value')}
                               disabled={!window.ethereum || !currentAccountAddress || valueChanging}
                    />
                    <Button type="submit"
                            variant="contained"
                            fullWidth
                            className={style.btn}
                            disabled={!window.ethereum || !currentAccountAddress || valueChanging}
                    >
                        set value
                    </Button>
                </form>
            </div>

            <div className={style.events}>
                <h2  className={style.blockTitle}>
                    Event logs
                </h2>

                <div className={style.table}>

                    <p className={style.tableLibel}>
                        ValueChanged Event
                    </p>


                    <div className={style.header}>
                        <p>Old value</p>
                        <p>New value</p>
                        <p>Timestamp</p>
                        <p>Address</p>
                    </div>

                    <div className={style.rows}>
                        {
                            events.map(({oldValue, newValue, timestamp, address}, key) => (
                                <div className={style.row} key={key}>
                                    <p>{oldValue}</p>
                                    <p>{newValue}</p>
                                    <p>{timestamp}</p>
                                    <p>{address}</p>
                                </div>
                            ))
                        }
                    </div>
                </div>


            </div>


        </div>
    )
})
